import { getTable, getTextConten, getElText } from "../util";
import { api, par } from "../i_api";

/** è·å–rap2å¹³å°çš„api */
export function getRap2Api(): api {
  console.log("å‚æ•°åˆ—è¡¨=========================");

  const par_el = <HTMLElement>(
    document.querySelector(
      "#root > article > div.body > article > div.body > div > article.InterfaceEditor > section:nth-child(2) > div.body > div > div.RSortableWrapper.depth-1",
    )
  );
  /** å‚æ•°åç§° å‚æ•°è¯´æ˜ è¯·æ±‚ç±»å‹ æ˜¯å¦å¿…é¡» æ•°æ®ç±»å‹ schema */
  const par_table = getTable(par_el, ".SortableTreeTableRow", ".td.payload", [
    undefined,
    (el) => {
      return el.querySelector("input").checked ? "true" : "false";
    },
  ]);

  const res_el = <HTMLElement>(
    document.querySelector(
      "#root > article > div.body > article > div.body > div > article.InterfaceEditor > section:nth-child(3) > div.body > div > div.RSortableWrapper.depth-1",
    )
  );
  /** å‚æ•°åç§° å‚æ•°è¯´æ˜ ç±»å‹ schema */
  const res_table = getTable(res_el, ".SortableTreeTableRow", ".td.payload", [
    undefined,
    (el) => {
      return el.querySelector("input").checked ? "true" : "false";
    },
  ]);
  console.log("å‚æ•°å’Œå“åº”", par_el, res_el, par_table, res_table);
  const get_level_list = (table: HTMLElement) => {
    const tr_list = table.querySelectorAll(".SortableTreeTableRow");
    return Array.from(tr_list).map((tr) => {
      const match = tr.parentElement.className.match(/depth(\d)/);
      if (match === null) return 0;
      else {
        return Number(match[1]) + 1;
      }
    });
  };
  const api: api = {
    url: getElText(".summary li:nth-child(1) a"),
    name: getElText(
      "#root > article > div.body > article > div.body > div > article.InterfaceEditor > div > div > span",
    ),
    describe: "",
    method: getElText(
      "#root > article > div.body > article > div.body > div > article.InterfaceEditor > div > ul > li:nth-child(2) > span > span:nth-child(2)",
    ),
    parList: reduction_tree(
      par_el,
      par_table.map((str_list) => {
        return {
          name: str_list[0].replace(/BODY$/, "").replace(/QUERY$/, ""),
          must: str_list[1] === "true",
          type: str_list[2],
          describe: str_list[5],
        };
      }),
      get_level_list,
    ),
    resList: reduction_tree(
      res_el,
      res_table.map((str_list) => {
        return {
          name: str_list[0],
          must: str_list[1] === "true",
          type: str_list[2],
          describe: str_list[5],
        };
      }),
      get_level_list,
    ),
  };
  console.log("æœ€ç»ˆç»“æœ", par_table, res_table, api);

  return api;
}

/** æ ¹æ®table è·å–åˆ°æ ‘çš„ç»“æ„ */
export function reduction_tree(table: HTMLElement, parList: par[], get_level_list: (table: HTMLElement) => number[]) {
  /** ç­‰çº§æ•°ç»„ [0,1,1,1,2,2,1,1] è¿™æ ·çš„ */
  const level_list = get_level_list(table);

  /** æœ€é«˜çº§ */
  const hierarchy: par[] = [];
  let current_hierarchy = hierarchy;
  for (let i = 0; i < level_list.length; i++) {
    const level = level_list[i];
    if (i === 0) {
      current_hierarchy.push(parList[i]);
      continue;
    }
    /** åŒçº§å…ƒç´  */
    if (level > level_list[i - 1]) {
      /** æŒ‰ä¸€èˆ¬è§„å¾‹æ¥è¯´å®ƒå°±æ˜¯ å½“å‰å±‚çº§æ•°ç»„æœ€åä¸€ä¸ªå…ƒç´ çš„ å­çº§ */
      const parent = current_hierarchy[current_hierarchy.length - 1];
      if (parent.children === undefined) {
        parent.children = [];
      }
      /** æŒ‡å‘ä¸‹ä¸€çº§ */
      current_hierarchy = parent.children;
    } else if (level === level_list[i - 1]) {
      /** åŒçº§çš„ */
    } else {
      /** å°äºçš„è¦æå‡å½“å‰å±‚çº§ */
      /** ä»æœ€é«˜çš„0çº§å¼€å§‹é™çº§,ç›´åˆ°å®ƒæ‰€åœ¨çš„ç­‰çº§ */
      let demotion_temp = hierarchy;
      /** å¼€å§‹é™çº§ */
      for (let i = 0; i < level; i++) {
        /** æŒ‰ä¸€èˆ¬è§„å¾‹æ¥è¯´ å®ƒä¸€å®šç”Ÿæˆåœ¨æœ€åä¸€ä¸ªå…ƒç´ çš„å­çº§ */
        demotion_temp = demotion_temp[demotion_temp.length - 1].children;
      }
      /** æŒ‡å‘å°†åˆ°çš„çº§åˆ« */
      current_hierarchy = demotion_temp;
    }
    /** å°†å…ƒç´ æ·»åŠ åˆ°å½“å‰å±‚çº§ */
    current_hierarchy.push(parList[i]);
  }
  return hierarchy;
}
